.skills {
  padding-left: 128px;
  padding-right: 125px; }
  .skills-title {
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    line-height: 48px;
    border-bottom: #CBD2D9 solid 0.5px;
    margin: 0px; }
  .skills-list {
    padding-top: 128px;
    display: grid;
    grid-template-columns: 1fr 1fr; }
    .skills-list__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 24px; }
    .skills-list__title {
      font-style: normal;
      font-weight: 600;
      font-size: 32px;
      line-height: 44px;
      margin: 0px;
      width: 379px; }
      .skills-list__title--green::before {
        content: '';
        width: 80px;
        height: 4px;
        background: #06C847;
        border-radius: 8px;
        display: block;
        margin-bottom: 24px; }
      .skills-list__title--violet::before {
        content: '';
        width: 80px;
        height: 4px;
        background: #653CAD;
        border-radius: 8px;
        display: block;
        margin-bottom: 24px; }
      .skills-list__title--blue::before {
        content: '';
        width: 80px;
        height: 4px;
        background: #2251CC;
        border-radius: 8px;
        display: block;
        margin-bottom: 24px; }
    .skills-list__item {
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      margin-bottom: 24px;
      margin-top: 0px;
      line-height: 40px; }
      .skills-list__item--green {
        list-style-image: url("/assets/img/check-circle.svg"); }
      .skills-list__item--violet {
        list-style-image: url("/assets/img/check-violet.svg"); }
      .skills-list__item--blue {
        list-style-image: url("/assets/img/check-blue.svg"); }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  color: #171F3F;
  background: white;
  font-family: 'Poppins';
  border: 16px solid #171F3F;
  scroll-behavior: smooth; }

.list {
  display: block;
  list-style-type: none;
  padding: 0;
  margin: 0; }

.list__item {
  background: #f0f0f0;
  padding: 16px;
  margin: 8px; }

@supports (display: grid) {
  .list {
    display: grid;
    grid-template-columns: 1fr 1fr; } }
